.cost-filter {
	color:white;
	padding: 0 1rem;
}
.costs-filter__control {
	display: flex;
	width:100%;
	align-items:center;
	justify-content: space-between;
	margin: 1rem 0;
}

.costs-filter label {
	font-weight:bold;
	margin-bottom: 0.5rem;
}

.costs-filter select {
	font:inherit;
	padding: 0.5rem 3rem;
	font-weight:bold;
	border-radius:6px;
}